/* General styles */
.container {
    padding: 20px;
    background-color: #e0f7fa;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .chartContainer {
    margin: 20px 0;
    width: 100%;
    max-width: 300px;
    height: 300px;
  }
  
  .heading {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .mainContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .readingContainer {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
    max-width: 300px;
  }
  
  .input {
    height: 30px;
    border: 1px solid #ccc;
    margin-bottom: 10px;
    padding: 0 10px;
    width: 100%;
  }
  
  .button {
    height: 30px;
    border: none;
    background-color: #4caf50;
    color: white;
    padding: 0 10px;
    margin: 10px 0;
    cursor: pointer;
    font-size: 14px;
    border-radius: 5px;
    width: 100%;
    display: block; /* Ensure buttons are block elements */
  }
  
  .button:hover {
    background-color: #45a049;
  }
  
  .motorButton {
    background-color: #2196f3;
  }
  
  .motorButton:hover {
    background-color: #1e88e5;
  }
  

  
  @media (max-width: 768px) {
    .mainContent {
      flex-direction: column;
      align-items: center; /* Center align items */
    }
  
    .chartContainer, .readingContainer {
      width: 90%; /* Take most of the width */
      max-width: none;
      margin: 10px 0; /* Add margin between elements */
    }
  
    .input, .button {
      width: 100%; /* Ensure inputs and buttons take full width */
      margin-bottom: 10px; /* Add margin between buttons */
    }
  
    .heading {
      font-size: 16px; /* Adjust font size for better readability */
    }
  
    .container {
      padding: 10px; /* Reduce padding for smaller screens */
    }
  }
  
  @media (max-width: 480px) {
    .mainContent {
      flex-direction: column;
      align-items: center; /* Center align items */
    }
  
    .chartContainer, .readingContainer {
      width: 100%; /* Take full width */
      max-width: none;
      margin: 10px 0; /* Add margin between elements */
    }
  
    .input, .button {
      width: 100%; /* Ensure inputs and buttons take full width */
      margin-bottom: 15px; /* Add more margin between buttons */
    }
  
    .button {
      height: 25px; /* Make buttons smaller */
      font-size: 12px; /* Reduce font size */
    }
  
    .chartContainer {
      height: 200px; /* Make chart smaller */
    }
  
    .heading {
      font-size: 14px; /* Adjust font size for better readability */
    }
  
    .container {
      padding: 5px; /* Reduce padding for smaller screens */
    }
  }